import { createRouter, createWebHistory } from 'vue-router'
import MainPage from '../pages/MainPage.vue'
import CartPage from '../pages/CartPage.vue'
import ProductPage from '../pages/ProductPage.vue'
import OrderPage from '../pages/OrderPage.vue'
import OrderInfoPage from '../pages/OrderInfoPage.vue'
import NotFoundPage from '../pages/NotFoundPage.vue'

const routes = [
  { path: 'final__exam/', name: 'main', component: MainPage },
  { path: 'final__exam/cart', name: 'cart', component: CartPage },
  { path: 'final__exam/product/:id', name: 'product', component: ProductPage },
  { path: 'final__exam/order', name: 'order', component: OrderPage },
  { path: 'final__exam/order/:id', name: 'orderInfo', component: OrderInfoPage },
  { path: 'final__exam/notFound(.*)*', name: 'notfound', component: NotFoundPage }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
